.App {
    text-align: center;
  }

  .farm-listing {
    border: 1px solid #ccc;
    margin: 10px;
    padding: 10px;
    display: inline-block;
    width: 200px;
  }

  .farm-listings {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
  }

  form {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin: 20px;
  }

  input {
    margin: 10px;
    padding: 10px;
    width: 200px;
  }

  button {
    padding: 10px 20px;
    cursor: pointer;
  }

  .status {
    margin: 20px;
    font-size: 24px;
  }